

import React, { lazy,Suspense,Component } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CHeader,
  CToggler,
  CHeaderBrand,
  CHeaderNav,
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import {
  Card,
  Col,
  Container,
  Row,
  Spinner,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"
import CIcon from '@coreui/icons-react'

import Carousel from 'react-bootstrap/Carousel'
import portfolio2 from '../dashboard/assets/img/portfolio/portfolio-2.jpg'
import { CContainer, CFade } from '@coreui/react'
import  { Redirect } from 'react-router-dom'
import InscriptionTournoiEcole from './InscriptionTournoiEcole'
import DetailTournoiEcole from './DetailTournoiEcole'
import InscriptionTournoiEntreprise from './InscriptionTournoiEntreprise'
import DetailTournoiEntreprise from './DetailTournoiEntreprise'
import ReactDOM from 'react-dom';
// import conf from '../../conf.js';
import { storage } from "../../firebase/firebase";
import $ from 'jquery'
import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
import TheFooter from '../../containers/TheFooter'
import conf from '../../conf.js';


const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class NosTournois extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
        tournoi:[],
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="success" />
     </div>,
       status:true,
      }
      this.handleDetailTournois.bind(this)
      this.handleParticiperTournois.bind(this)
  }
  
  componentDidMount() {

    const requestOptions = {
      method: 'GET',
     
    };

    fetch(conf.url+"tournoi/tournois",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data tournoi===>",data)
      this.setState({status:false})
      try{
        if(data.length){
          this.setState({tournoi:data})
          data.map((el,idx)=>{
            try{
              if(el.multimedia.file!=undefined && el.multimedia.file!=''){
                storage.ref("images")
                .child(el.multimedia.file).getDownloadURL()
                .then((url) => {
                  
                    data[idx].multimedia.file=url
                    this.setState({tournoi:data,status:false})
                
                })
                }
            }catch(error){

            }
          
           })
        }
        
       }catch(error){
        this.setState({tournoi:[]})
       }
     }
     
     );
  
  };


  handleParticiperTournois=(type,data)=>{
    localStorage.setItem('idAcceuilTournoi',data.id);
    localStorage.setItem('prixTournoi',data.prixPartSa);
   if(type=="ecole"){
    ReactDOM.render(<div>{<InscriptionTournoiEcole/>}</div>, document.getElementById('page'));
   }else{
    ReactDOM.render(<div>{<InscriptionTournoiEntreprise/>}</div>, document.getElementById('page'));
   }
 
  }
  handleDetailTournois=(type,data)=>{
    var logo=""
    localStorage.setItem('detail_tournoi_visiteur', JSON.stringify({matchs:data.matchs,logo:data.multimedia.file,id:data.id,date_debut:data.dateDebut,
      date_fin:data.dateFin,date_fin_inscription:data.dateFinInscription,prix:data.prixPartSa,date_debut_inscription:data.dateDebutInscription}));


      if(type=="ecole"){


        ReactDOM.render(<div>{<DetailTournoiEcole/>}</div>, document.getElementById('page'));
     }else{
      
        ReactDOM.render(<div>{<DetailTournoiEntreprise/>}</div>, document.getElementById('page'));
     }
  
   
  }

  render() {

    return (
      <>
        {this.state.ok}
        {/* <CContainer fluid>
          <Suspense fallback={loading}> */}
          {
        (!this.state.status)?

        (
            <CFade>
              <div className="mt-5"></div>
                <section id="tournois" class="team section-bg">
                  <div class="section-title mt-5">
                      <h2>Nos tournois</h2>
                  </div>

                  <div className="ml-1 mr-1" class="row portfolio-container">
                      
                      {
                        this.state.tournoi.map((element,idx)=>(

                      
                        <div className="mt-3" class="col-lg-4 col-md-4 portfolio-item filter-web" >
                        <div>

                          <figcaption class="text-center mt-2 mb-2 font-weight-bold">
                            <span style={{backgroundColor:"#194D33",textAlign:'center',float:'center'}} class="badge rounded-pill text-center">{element.typeTournoi=="TOURNOI_INTER_ENTREPRISE"?"Tournoi inter entreprise":"Tournoi inter cole"}</span>
                          </figcaption>

                          <figure class="terrain">
                            {
                              (element.multimedia!=null && element.multimedia.file!='')?
                              (<img style={{width:"540px",height:"250px"}} src={element.multimedia.file} class="img-fluid" alt=""/>):
                              (<img style={{width:"540px",height:"250px"}} src={portfolio2} class="img-fluid" alt=""/>)
                            }
                          
                            
                              <figcaption class="text-center mt-2 font-weight-bold">{element.nom}</figcaption>
                              
                              {/* <span style={{backgroundColor:"#194D33",textAlign:'center',float:'center'}} class="badge rounded-pill text-center">Type de tournoi</span> */}
                            
                            
                          </figure>
                          {
                              (element.typeTournoi==conf.type_tournoi.TOURNOI_INTER_ECOLE)?
                              (
                                <div class="row">
                                  <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12"><a style={{float:'left'}}   onClick={this.handleDetailTournois.bind(this,"ecole",element)} class="get-started-btn2  text-center a">Dtails</a></div>
                                  <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12"><a style={{float:"right"}}  onClick={this.handleParticiperTournois.bind(this,"ecole",element)}  class="get-started-btn  text-center a">Participer</a></div>
                                </div>
                              ):(
                                <div class="row" >
                                  <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12"><a style={{float:'left'}}   onClick={this.handleDetailTournois.bind(this,"entreprise",element)} class="get-started-btn2 text-center a">Dtails</a></div>
                                  <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12"><a style={{float:"right"}}   onClick={this.handleParticiperTournois.bind(this,"entreprise",element)}  class="get-started-btn  text-center a">Participer</a></div>
                                </div>
                              )
                          }
                        
                        </div>
                      </div>
                        ))
                      }
                  </div>
                    
              
                </section>
              
           
            </CFade>):(<>{this.state.page}</>)
          }
          {/* </Suspense>
        </CContainer> */}
      </>
    )
  }
}

export default NosTournois
